.BoardHeader {
  width: 100vw;
  height: 88px;
  background-color: white;
}

#BoardGrid {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
  align-items: center;
  padding-inline: 35px;
  overflow-x: scroll;
  cursor: pointer;
}

#BoardGrid::-webkit-scrollbar {
  display: none; /* for Chrome, Safari, and Opera */
}

.BoardCard-Active {
  width: 191px;
  min-width: 191px;
  height: 60px;
  background-color: #4680fc;
  border-radius: 17px;
  margin-left: 20px;
  display: flex !important;
  justify-content: flex-start !important;
  align-items: center !important;
  color: white;
  font-weight: 500;
  font-size: 19px;
  padding-inline: 25px !important;
  cursor: pointer;
  margin-left: 20px !important;
}

.BoardCard-Inactive {
  /* minWidth: 191px !important; */
  min-width: 191px;
  position: relative;
  height: 60px;
  background-color: white;
  border-radius: 17px;
  margin-left: 20px;
  display: flex !important;
  justify-content: flex-start !important;
  align-items: center !important;
  color: #313a46;
  font-weight: 500;
  font-size: 18px;
  border: 1px solid #00000016;
  padding-inline: 10px !important;
  cursor: pointer;
  white-space: nowrap;
  transition: all 0.1s ease-in-out;
}

.BoardCard-Inactive .Button {
  width: 30px;
  height: 30px;
  background-color: white;
  margin-left: 15px;
}

.BoardCard-Active p {
  cursor: pointer;
}

.BoardCard-Inactive p {
  margin-left: 10px;
  cursor: pointer;
}

.BoardCard-Inactive:hover {
  background-color: #00000016;
  transition: all 0.1s ease-in-out;
}

@media only screen and (max-width: 800px) {
  .BoardHeader {
    height: 66px;
    /* transform: scale(0.5); */
  }

  .BoardCard-Active {
    min-width: 150px;
    height: 48px;
    border-radius: 13px;
    font-size: 15px;
    margin-left: 0px;
  }
  .BoardCard-Inactive {
    min-width: 150px;
    height: 48px;
    border-radius: 13px;
    font-size: 15px;
  }

  .BoardCard-Inactive .Button {
    width: 20px;
    height: 20px;
    background-color: white;
    margin-left: 10px;
  }
}
