body {
  background-color: #f5f6fa;
  transition: all 0.08s ease-in-out;

}

.TaskView {
  width: 30% !important;
  height: calc(100vh - 88px - 87px);
  /* background-color: aquamarine; */
  border: none !important;
  padding: 30px !important;
  box-shadow: none !important;
}

.TaskView .Container {
  width: 100%;
  height: 100%;
  background-color: white;
  border-radius: 3vw;
  box-shadow: rgba(66, 64, 77, 0.05) 0px 48px 100px 0px;
  padding: 10px;
  overflow: hidden;
}

.TaskInputView {
  width: 40% !important;
  height: auto;
  /* background-color: aquamarine; */
  border: none !important;
  padding: 30px !important;
  box-shadow: none !important;
}

.TaskInputView .Container {
  width: 100%;
  height: 15vw;
  background-color: white;
  border-radius: 3vw;
  box-shadow: rgba(66, 64, 77, 0.05) 0px 48px 100px 0px;
  padding: 25px;
  overflow: hidden;
}

.Heading {
  width: 100%;
  height: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
  /* background-color: aqua; */
  font-size: 2vw;
  color: #313a46;
}

.ListDiv {
  width: 100%;
  height: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-block: 10px;
  overflow-y: scroll;
}

.TaskCard {
  width: 90%;
  height: fit-content;
  padding-block: 0.8vw;
  padding-inline: 0.5vw;
  /* min-height: 4vw; */
  border-radius: 1vw;
  display: flex !important;
  justify-content: space-evenly !important;
  align-items: center !important;
  color: #313a46;
  font-weight: 500;
  font-size: 2vw;
  border: 1px solid #00000016;
  margin-top: 1vw;
}

.TextView {
  width: 70%;
  height: fit-content;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.CompletedTextView {
  width: 70%;
  height: fit-content;
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 1vw !important;
  line-height: 1.3vw;
}

.UtilityView {
  width: 20%;
  height: fit-content;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.SuccessView {
  width: 10%;
  /* height: 100%; */
  /* background-color: red; */
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  /* margin-left: 10px; */
}

.Button {
  width: 1.8vw;
  height: 1.8vw;
  border-radius: 100px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  transition: all 0.04s ease-in-out;

}

.Button:hover{
  background-color: #00000016;
  transition: all 0.04s ease-in-out;

}


.TaskCard .Button svg{
  width: 1.1vw ;
  height: 1.1vw;
} 


.TaskInputView .Heading{
  height: 30%;
}

.SearchDiv {
  width: 100%;
  height: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.SearchDiv .Input {
  width: 80%;
  height: 2.9vw;
  background-color: #f5f6fa;
  padding-inline: 0.9vw;
  border-radius: 1vw;
}

input {
  background-color: transparent;
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  font-size: 1vw;
}

.Button-off{
  width: 1vw;
  height: 1vw;
  border: 0.1px solid #313a46a3;
  border-radius: 100vw;
  transition: all 0.04s ease-in-out;

}

.Button-off:hover{
  background-color: #4680fc49;
  border: 2px solid #4680fc;
  transition: all 0.04s ease-in-out;

}





