@media only screen and (max-width: 1300px) {
  .TaskInputView {
    padding-inline: 10px !important;
  }

  .TaskInputView .Container {
    height: 150px;
  }

  .TaskCard {
    padding-inline: 10px !important;
  }
  .TaskView {
    padding-inline: 10px !important;
    height: 450px;
  }

  .TaskView .Container {
    width: 100%;
    height: 50vw !important;
    background-color: white;
    border-radius: 3vw;
    box-shadow: rgba(66, 64, 77, 0.05) 0px 48px 100px 0px;
    padding: 10px;
    overflow: hidden;
  }
}

@media only screen and (max-width: 800px) {

 

  .MainContainer {
    padding-inline: 4vw !important;
    display: flex !important;
    flex-direction: column !important; 
    
  }

  .Heading{
    font-size: 5vw;
  }

  .TaskInputView .Heading{
    font-size: 5vw;
    height: 40%;

  }

  .TaskInputView{
    width: 100% !important;
  }

  .TaskInputView .Container {
    height: 30vw;
    border-radius: 5vw;
    padding: 0;
    
  }


  .TaskInputView .Container .SearchDiv .Input {
    border-radius: 3vw;
    height: 10vw;
    font-size: 2vw;
  }


  .TaskInputView .Container .SearchDiv input {
    font-size: 3vw;
    padding-inline: 4px;
  }
  .TaskView {
    width: 100% !important;
    height: fit-content;
  }

  .TaskView .Container {
    height: 400px !important;
    padding-block: 30px !important;

  }


  .TaskCard{
    padding: 3vw;
    border-radius: 3vw;
  }

  .TaskCard .CompletedTextView p{
    font-size: 3vw ;
    transform: translateY(1px);
  }


  .TaskCard .Button {
    width: 4vw ;
    height: 4vw;
  }

  .TaskCard .Button svg{
    width: 3.3vw ;
    height: 3.3vw;
  } 

  .TaskCard input{
    font-size: 3vw;
  } 

  .Button-off{
    width: 4vw;
    height: 4vw;
    border: 0.1px solid #313a46a3;
    border-radius: 100vw;
    transition: all 0.04s ease-in-out;
  
  }
 
}
